.github-logo {
  width: 160px;
  position: relative;
  left: 100%;
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%); }

.title, .description {
  text-align: center;
  color: #fff0f0;
  word-wrap: break-word; }

body {
  font-family: 'Open Sans', sans-serif; }

.background {
  height: 100vh;
  background-image: url("../../images/backgrounds/6.jpg");
  background-repeat: no-repeat;
  background-position: right top;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  padding-right: 0px;
  padding-left: 0px; }

.painel {
  width: 97vw;
  height: 95vh;
  margin-top: 2.5vh;
  margin-bottom: 2.5vh;
  margin-left: 1.5vw;
  margin-right: 1.5vw;
  background-color: rgba(0, 0, 0, 0.6); }

.logo-img {
  max-width: 220px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 4%; }
  @media (max-width: 720px) {
    .logo-img {
      padding-top: 10%; } }
  @media (max-width: 720px) and (orientation: landscape) {
    .logo-img {
      padding-top: 0vh;
      max-width: 121px; } }

.title {
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  font-size: 40px; }
  @media (orientation: landscape) {
    .title {
      font-size: 32px; } }

.description {
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  padding-top: 7.5px;
  padding-left: 5vw;
  padding-right: 5vw; }
  @media (max-width: 720px) {
    .description {
      font-size: 18px; } }

.tecnologias {
  font-weight: bold;
  padding-top: 2vh;
  color: rgba(255, 255, 255, 0.8);
  text-shadow: 1.5px 1.5px rgba(0, 0, 0, 0.5);
  font-weight: 600; }
  @media (max-width: 720px) {
    .tecnologias {
      padding-top: 2vh;
      padding-right: 15vw;
      padding-left: 15vw;
      font-size: 16px; } }
  @media (max-width: 720px) and (orientation: landscape) {
    .tecnologias {
      display: none; } }

.carousel-img {
  max-height: 18vh; }

.slide {
  padding-top: 15px; }
  @media (max-width: 720px) {
    .slide {
      padding-top: 5px; } }

.download-button {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  background: transparent;
  border: 2px solid #039bc6;
  position: relative;
  top: 7px;
  left: 7px;
  font-weight: bold;
  -webkit-box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3);
  box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3);
  text-shadow: 2px 2px rgba(0, 0, 0, 0.3); }
  .download-button:hover {
    background-color: #04c2f8;
    border-color: #04c2f8; }
  .download-button:active {
    background-color: #0387ad !important;
    border-color: #0387ad !important; }
  .download-button:focus {
    -webkit-box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3);
    box-shadow: 2.5px 2.5px rgba(0, 0, 0, 0.3); }

.carousel-item-element {
  color: #fff0f0;
  font-weight: bold;
  font-size: 20.8px;
  height: 4vh;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5); }

@media (max-width: 750px) and (orientation: landscape) {
  .carousel-item-element {
    display: none; } }

